
import cookie from '../../../static/js/cookie'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Loading from 'vux/src/components/loading/index.vue'

export default {
name: 'Guangguang',
data () {
return {
msgContent:0,
show:false,
host:config.DOMAIN_HOST,
dialogType:0,
list: [],
service: [],
display: 'none',
addProjectInfo: 'none',
editProjectInfo: 'none',
content: '',
tcPrice: '',
tcDes: '',
title: '',
id:0,
tid:0,
listId: [],
priceList: [],
minprice: 0,
addTitle:'',
addPrice:'',
addDetail:'',
editTitle:'',
editPrice:'',
editDetail:'',
editCover:'',
editBig:'',
editId:0,
delId:0,
modelCoverSrc:'',
coverfiles:'',
modelBigSrc:'',
bigfiles:'',
delProjectInfo:'none',
isShowModelCover: false,
option: {
img: '',
outputSize: 0.5,
full: true,
outputType: 'png',
fixedBox: true,
original: false,
canMoveBox: true,
canMove:true,
centerBox:true,
high: true,
autoCrop:true,
},
previews: {},
}
},
directives: {
TransferDom
},
components: {
XButton,
Loading,
Flexbox,
FlexboxItem
},
created () {
if(cookie.getCookie('mid') == null){
this.getWechatAuth()
}
this.Service()
},
mounted () {
if(cookie.getCookie('mid') == null){
this.getWechatAuth()
}
this.Service()
let header = document.getElementById('header').offsetHeight
window.msgContent = document.documentElement.clientHeight
this.msgContent = (window.msgContent - header) + 'px'
},
methods: {


realTime(data) {
this.previews = data
},
uploadImgBig(e, type) {
this.uploadImg(e, type)
},
uploadImgCover(e, type) {
this.uploadImg(e, type)
},
uploadImg(e, type){

this.dialogType = type

let file = e.target.files[0]
if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) {
alert('图片类型必须是.gif,jpeg,jpg,png,bmp中的一种')
return false
}
let reader = new FileReader()
reader.onload = (e) => {
let data
if (typeof e.target.result === 'object') {

data = window.URL.createObjectURL(new Blob([e.target.result]))
} else {
data = e.target.result
}
this.option.img = data
}



reader.readAsArrayBuffer(file)
this.isShowModelCover = true
},
imgLoad(msg) {
console.log(msg)
},
dialogFormCancel(){
this.close()
},
dialogSmallFormQuery(){
this.$refs.cropper.getCropBlob((data) => {
this.modelCoverSrc =  window.URL.createObjectURL(data)
this.coverfiles = data
this.close()
})
},
dialogBigFormQuery(){
this.$refs.cropper.getCropBlob((data) => {
this.modelBigSrc =  window.URL.createObjectURL(data)
this.bigfiles = data
this.close()
})
},
close(){
this.option = {
img: '',
outputSize: 0.2,
full: true,
outputType: 'png',
canMove: true,
fixedBox: true,
original: false,
canMoveBox: true,
canMove:false,
centerBox:true,
high: true,
autoCrop:true,
};
this.dialogType = 0
this.previews = {}
if(this.option.img) {
this.$refs['cropper'].imgs = "";
}
this.option.img = ''
this.isShowModelCover = false
},
rotateRight(){
this.$refs.cropper.rotateRight()
},
rotateLeft(){
this.$refs.cropper.rotateLeft()
},

canselServic (i) {
this.list.splice(i, 1)
this.$vux.toast.text('已取消')
},
CancelDelProject () {
this.delProjectInfo = 'none'
},
queryDelService (id) {
this.delProjectInfo = 'block';
this.delId = id
},
queryDelProject(){
this.delService(this.delId)
},
cancel () {
this.display = 'none'
},
editProject (title,price,detail,id,cover,big) {
this.editProjectInfo = 'block'
this.editTitle = title
this.editPrice = price
this.editDetail = detail
this.editCover = cover
this.editBig = big
this.editId = id

document.body.style.position='fixed';
document.body.style.width='100%';

},
cancelProject () {
this.editProjectInfo = 'none'
document.body.style.position='static';
},
queryProject () {
this.editService(this.editTitle, this.editPrice, this.editDetail, this.editId, this.editCover, this.editBig)
document.body.style.position='static';
},
addService () {
this.addProjectInfo = 'block';
document.body.style.position='fixed';
document.body.style.width='100%';
},
cancelAddProject () {
this.addProjectInfo = 'none'
document.body.style.position='static';
},
queryAddProject () {
this.addServiceInfo(this.addTitle, this.addPrice, this.addDetail, this.coverfiles, this.bigfiles)
document.body.style.position='static';
},
addServiceInfo(title, price, des, cover, big){
if(price < 1){
this.$vux.toast.text('最低价格不能小于1元')
return false
}
this.show = true
let param = new FormData();
param.append('cover',cover,'cover.png');
param.append('big',big,'big.png');
return this.$api.upg(param).then(res => {
let type = 'post'
let data = {
'title':title,
'price':price,
'des':des,
'status':1,
'mid':cookie.getCookie('mid'),
'teacher':cookie.getCookie('tid'),
'cover':res.data.info.cover,
'big':res.data.info.big,
}
return this.$api.project(data, type).then(res => {
this.service = this.service.concat(res.data)
this.Service()
this.addProjectInfo = 'none'
this.$vux.toast.text('新增成功')
this.show = false
this.defaultSetting()
})
})
},
defaultSetting(){

this.addTitle=''
this.addPrice=''
this.addDetail=''
this.editTitle=''
this.editPrice=''
this.editDetail=''
this.editCover=''
this.editBig=''
this.modelCoverSrc=''
this.coverfiles=''
this.modelBigSrc=''
this.bigfiles=''
this.editId=0
this.delId=0
this.option.img=''

},
editService (title, price, des, id, cover, big) {
if(price < 1){
this.$vux.toast.text('最低价格不能小于1元')
return false
}
this.show = true
let type = 'put'
if(this.coverfiles && this.bigfiles){
let param = new FormData();
param.append('cover',this.coverfiles,'cover.png');
param.append('big',this.bigfiles,'big.png');
return this.$api.upg(param).then(res => {
let data = {
'title':title,
'price':price,
'des':des,
'status':1,
'cover':res.data.info.cover,
'big':res.data.info.big,
}
this.project(data, type, id)
})
}else if(this.coverfiles){
let param = new FormData();
param.append('cover',this.coverfiles,'cover.png');
return this.$api.upg(param).then(res => {
let data = {
'title':title,
'price':price,
'des':des,
'status':1,
'cover':res.data.info.cover,
}
this.project(data, type, id)
})
}else if(this.bigfiles){
let param = new FormData();
param.append('big',this.bigfiles,'big.png');
return this.$api.upg(param).then(res => {
let data = {
'title':title,
'price':price,
'des':des,
'status':1,
'big':res.data.info.big,
}
this.project(data, type, id)
})
}else{
let data = {
'title':title,
'price':price,
'des':des,
'status':1,
}
this.project(data, type, id)
}
},
project(data, type, id){
return this.$api.project(data, type, id).then(res => {
this.service = this.service.concat(res.data)
this.Service()
this.editProjectInfo = 'none'
this.$vux.toast.text('修改成功')
this.modelCoverSrc = ''
this.modelBigSrc = ''
this.show = false
})
},
getWechatAuth: function () {
return this.$api.wechatAuth().then(res => {
window.location.href = res.data.result
}).catch(err => {
return err
})
},
delService (id) {
let type = 'delete'
return this.$api.project(0, type, id).then(res => {
this.Service()
this.$vux.toast.text('删除成功')
this.delProjectInfo = 'none'
})
},
saveService (title, price, des, i) {

let type = 'post'
let data = {
"title": title,
"sort": 1,
"price": price,
"des": des,
"belong": this.id,
"mid": cookie.getCookie('mid'),
"tid": cookie.getCookie('tid'),
}
return this.$api.service(data, type, 0).then(res => {
this.service = this.service.concat(res.data)
this.list.splice(i, 1)
this.Service()
this.$vux.toast.text('新增成功')
}).catch(err => {
this.$vux.toast.text(err)
return err
})
},

Service (){
let that = this;
that.tid = cookie.getCookie('tid')
layui.use('flow', function(){
let flow = layui.flow;
let $ = layui.jquery
,element = layui.element;

flow.load({
elem: '#all' 
,isAuto:true
,scrollElem: '#all' //滚动条所在元素，一般不用填，此处只是演示需要。
,loadMore:'#loadMore'
,done: function(page, next){

that.$api.shop({'page': page,'m':that.tid}).then(res => {
if(page == 1){
that.service = []
}
that.service = that.service.concat(res.data.results)
next('', page < Math.ceil(res.data.count / 10 ));
})
}
});
});
},

findId: function (id) {
return this.listId.indexOf(id)
}
},
}



